import Verify from "./../verify/verify"
export default {
	data() {
		return {
			showMode:'fixed',
		}
	},
	components:{
		Verify
	},
	methods: {
		showVerify(){
			this.$refs.verify.show();
		},
		success(data){
			console.log(data,"成功返回");
		},
		fixedClick(){
			this.showMode = "fixed"
		},
		popClick(){
			this.showMode = "pop"
		}
	},
}
